import React from 'react';

let stFont = {fontSize: '60px'};
let stMarg = {marginBottom: '0px'};
let stTd = {padding: '5px 10px 5px 10px'};

const StepTen = (props) => (
  <table>
    <tbody>
    <tr>
      <td><div className="text-center alert alert-info" role="alert" style={stMarg}>Reducer</div></td>
      <td><div className="text-center"><i style={stFont} className="fa fa-long-arrow-right"></i></div></td>
      <td><div className="text-center alert alert-info" role="alert" style={stMarg}>Store</div></td></tr>
    <tr>
      <td><div className="text-center"><i style={stFont} className="fa fa-long-arrow-up"></i></div></td>
      <td></td>
      <td><div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div></td>
    </tr>
    <tr>
      <td><div className="text-center alert alert-info" role="alert" style={stMarg}>Action</div></td>
      <td><div className="text-center"><i style={stFont} className="fa fa-long-arrow-left"></i></div></td>
      <td><div className="text-center alert alert-success" style={stMarg}>React</div></td>
    </tr>
    </tbody>
  </table>
);

export default StepTen;



