/*jshint esversion:6 */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../actions/steps';

class StepHead extends Component {

  constructor(props) {
    super(props);

    this.getStep = () => this._getStep();
  }

  render() {
    return (
      <div>
        <h4>Step {this.props.step.currentStep}: {this.getStep()}</h4>
      </div>
    );
  }

  _getStep(){
    switch (this.props.step.currentStep) {
      case 1:
        return 'The One direction of flow';
      case 2:
        return 'Libraries used and code location';
      case 3:
        return 'The 3 Principles of Redux';
      case 4:
        return 'React Component';
      case 5:
        return 'Action/ Action Creator';
      case 6:
        return 'The Reducer';
      case 7:
        return 'The Store';
      case 8:
        return 'Middleware';
      case 9:
        return 'All';
      case 10:
        return 'Further Reading';
      default:
        return 'Postage Stamp Overview';
    }
  }

}

function mapStateToProps(state){
  return { step: state.step };
}

export default connect(mapStateToProps, actions)(StepHead);



