import React from 'react';

let stFont = {fontSize: '60px'};
let stMarg = {marginBottom: '0px'};
let stTd = {padding: '5px 10px 5px 10px'};

const StepZero = (props) => (
  <table>
    <tbody>
      <tr>
        <td><div className="text-center alert alert-success" style={stMarg}>React</div></td>
        <td style={stTd}> Component is subscribed to Store to receive updates</td>
      </tr>
      <tr>
        <td><div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div></td>
        <td></td>
      </tr>
      <tr>
        <td><div className="text-center alert alert-info" role="alert" style={stMarg}>Action</div></td>
        <td style={stTd}>Plain JavaScript Object. <br /> Has minimum 2 name value pairs, 1st must be type.</td>
      </tr>
      <tr>
        <td><div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div></td>
        <td></td>
      </tr>
      <tr>
        <td><div className="text-center alert alert-info" role="alert" style={stMarg}>Reducer</div></td>
        <td style={stTd}> A pure function. <br /> Takes in state + action. Returns new state</td>
      </tr>
      <tr>
        <td><div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div></td>
        <td></td>
      </tr>
      <tr>
        <td><div className="text-center alert alert-info" role="alert" style={stMarg}>Store</div></td>
        <td style={stTd}>JavaScript Object<br /> Holds state</td></tr>
      <tr>
        <td><div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div></td>
        <td></td>
      </tr>
      <tr>
        <td><div className="text-center alert alert-success" style={stMarg}>React</div></td>
        <td style={stTd}> Component is updated</td>
      </tr>
    </tbody>
  </table>
);

export default StepZero;



