import React from 'react';

let stFont = {fontSize: '60px'};
let stMarg = {marginBottom: '0px'};
let stTd = {padding: '5px 10px 5px 10px'};
let stFont30 = {fontSize: '30px'};
let stAlignTop = {verticalAlign: 'top'};
let stTopMarge = {verticalAlign: 'top',padding: '5px 10px 5px 10px'};

const StepSix = (props) => (
  <table>
    <tbody>
      <tr>
        <td style={stAlignTop}>
        <div className="text-center alert alert-success" style={stMarg}>React</div>
        <div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div>
        <div className="text-center alert alert-info" role="alert" style={stMarg}>Action</div>
        <div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div>
        <div className="text-center alert alert-info" role="alert" style={stMarg}>Reducer</div>
        <div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div>
        </td>
        <td style={stTopMarge}>
          <table>
          <tbody>
            <tr><td>Example Reducer</td></tr>
            <tr><td>location: src/reducers/index.js</td></tr>
            <tr><td>library: redux</td></tr>
            <tr><td><br />The Reducer is a function that receives state and action then returns the new state.
            Basically it takes the state, the action to be made to the state and then returns the new changed state. <br /><br />
            Redux is an implementation of the Flux methodology.
            Where Redux differs from other Flux libraries is it only has 1 store.
            The other libraries have many stores.
            What Redux does differently is it has 1 store but can have many reducers.
            (Think of it 1 pie, many slices of pie. Where each slice is a Reducer and the pie is the store.).
            Each reducer function executes actions on a particular part of state.
            If your app is small you are likely to only have 1 reducer, but as it grows in scale or complexity you are likely to have many reducers.

            </td></tr>
            <tr><td><pre>{`import { combineReducers } from 'redux';
import steps from './reducer_steps';

const rootReducer = combineReducers({
  step: steps
});

export default rootReducer;`}</pre></td></tr>
              <tr><td>Note in the example above the Reducer is called step and
              contained in the reducer_steps.js file.
              The rootReducer combines 1 or more reducers together to act as 1.
              However when passed through to state as props, the step reducer
              will be accessed as props.step.</td></tr>
              <tr><td><pre>{`//reducer_steps.js
import {
    CHANGE_STEP
} from '../actions/steps';


const INITIAL_STATE = {
    currentStep: 0,
    stepLimit: 11,
    anchorPrefix: 'atat_'
};

export default function(state = INITIAL_STATE, action) {
    if (typeof state === undefined) {
        return INITIAL_STATE;
    }

    switch (action.type) {
        case CHANGE_STEP:
            return Object.assign({}, state, {
                currentStep: action.cargo
            });
        default:
            return state;
    }
    return state;
}`}</pre></td></tr>
              <tr><td>In the example above the Reducer imports the action type constant, CHANGE_STEP. It then defines the initial state.
              This is a plain javascript object with name value pairs. The initial state can be set or, alternatively a previous state can be set.
              This is will be either from local storage or from a database/datastore on the server.<br />
              The default function is simply a switch statement. It receives the action and the payload changes.<br /><br />
              Notice the return from the switch statement is either the current state - if no action defined or a new state.
              The new state is an object assign - a new object with the change made to the state.<br />
              This allows the state to be stored as an array of states allowing for the actiona and state to be stored.<br />
              This facilitates having front end debugging capabilities as each action and state can be stored and sent back to the server.
              </td></tr>
            </tbody>
          </table></td>
      </tr>
    </tbody>
  </table>
);

export default StepSix;



