import React from 'react';

let stFont = {fontSize: '60px'};
let stMarg = {marginBottom: '0px'};
let stTd = {padding: '5px 10px 5px 10px'};
let stFont30 = {fontSize: '30px'};
let stAlignTop = {verticalAlign: 'top'};

const StepFour = (props) => (
  <table>
    <tbody>
      <tr>
        <td style={stAlignTop}><div className="text-center alert alert-success" style={stMarg}>React</div>
        <div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div></td>
        <td style={stTd}><table>
          <tbody>
            <tr><td>Example React.js Component.</td></tr>
            <tr><td>location: src/components/root.js</td></tr>
            <tr><td>libraries: react, react-redux</td></tr>
            <tr><td><br />At the beginning of the component file will be the import of the libraries.</td></tr>
            <tr><td><pre>{`import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../actions/steps';`}</pre></td></tr>
            <tr><td>Please note there are several different ways to include libraries, this can be import or require.<br /><br /></td></tr>
            <tr><td><br />Then there is the component, of which there are several ways to declare the component.</td></tr>
            <tr><td><pre>{`class Root extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div>
        <div>
          <h1>Redux, the basics in 10 steps</h1>
          <NavMenu />
          <StepHead />
        </div>
        <StepBody />
      </div>
    );
  }
}`}</pre></td></tr>
            <tr><td><br />Below the component is where the connection between the React.js
            Component and the Redux store happens.</td></tr>
            <tr><td><pre>{`function mapStateToProps(state){
  return { step: state.step };
}

export default connect(mapStateToProps, actions)(Root); `}</pre></td></tr>
            <tr><td><br />The connect function binds React Component to Redux, the state is the
            data into the component via props and the action is change out of the component.
            The example below breaks down what is happening.<br /><br /></td></tr>
            <tr><td>
                  <table>
                  <tbody>
                    <tr><td></td><td><div className="text-center">IN</div></td><td></td></tr>
                    <tr><td></td><td><div className="text-center">State</div></td><td></td></tr>
                    <tr><td></td><td><div className="text-center"><i style={stFont30} className="fa fa-long-arrow-down"></i></div></td><td></td></tr>
                    <tr><td>connect(&nbsp;</td><td> mapStateToProps,</td><td>&nbsp; mapDispatchToProps )</td></tr>
                    <tr><td></td><td></td><td><div className="text-center"><i style={stFont30} className="fa fa-long-arrow-down"></i></div></td></tr>
                    <tr><td></td><td></td><td><div className="text-center">Action</div></td></tr>
                    <tr><td></td><td></td><td><div className="text-center">OUT</div></td></tr>
                  </tbody>
                  </table>
            </td></tr>
            </tbody>
          </table></td>
      </tr>
    </tbody>
  </table>
);

export default StepFour;



