/*jshint esversion:6 */
import {
    CHANGE_STEP
} from '../actions/steps';


const INITIAL_STATE = {
    currentStep: 0,
    stepLimit: 11,
    anchorPrefix: 'atat_'
};

export default function(state = INITIAL_STATE, action) {
    if (typeof state === undefined) {
        return INITIAL_STATE;
    }

    switch (action.type) {
        case CHANGE_STEP:
            return Object.assign({}, state, {
                currentStep: action.cargo
            });
        default:
            return state;
    }
    return state;
}



