/*jshint esversion:6 */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../actions/steps';
import StepZero from './step0';
import StepOne from './step1';
import StepTwo from './step2';
import StepThree from './step3';
import StepFour from './step4';
import StepFive from './step5';
import StepSix from './step6';
import StepSeven from './step7';
import StepEight from './step8';
import StepNine from './step9';
import StepTen from './step10';

class StepBody extends Component {

  constructor(props) {
    super(props);

    this.getStep = () => this._getStep();
  }

  render() {

    return (
      <div>
        {this.getStep()}
      </div>
    );
  }

  _getStep(){
    switch (this.props.step.currentStep) {
      case 1:
        return <StepOne />;
      case 2:
        return <StepTwo />;
      case 3:
        return <StepThree />;
      case 4:
        return <StepFour />;
      case 5:
        return <StepFive />;
      case 6:
        return <StepSix />;
      case 7:
        return <StepSeven />;
      case 8:
        return <StepEight />;
      case 9:
        return <StepNine />;
      case 10:
        return <StepTen />;
      default:
        return <StepZero />;
    }
  }

}

function mapStateToProps(state){
  return { step: state.step };
}

export default connect(mapStateToProps, actions)(StepBody);



