/*jshint esversion:6 */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../actions/steps';
import NavMenuNormal from './navmenunormal';
import NavMenuCurrent from './navmenucurrent';

class NavMenu extends Component {

  constructor(props) {
    super(props);

    this.getMenu = () => this._getMenu();
    this.stripPrefix = (a,b) => this._stripPrefix(a,b);
    this.handleMenuClick = (e) => this._handleMenuClick(e);

  }

  render() {
    return (
      <div className="btn-group" role="group" aria-label="...">
        {this.getMenu()}
      </div>
    );
  }

  _stripPrefix(a,b){
    return a.substring(b.length, a.length);
  }

  _handleMenuClick(e){
    e.preventDefault();
    var a = (this.stripPrefix(e.target.id, this.props.step.anchorPrefix) *1);
    this.props.doChangeStep(a);
  }

  _getMenu(){
    var i=0;
    var l=this.props.step.stepLimit;
    var rows = [];

    for(i=0;i<l;i++){
      switch (i) {
        case this.props.step.currentStep:
          rows.push(<NavMenuCurrent key={i} stepno={i} />);
          break;
        default:
          rows.push(<NavMenuNormal key={i} stepno={i} handleClick={this.handleMenuClick} prefix={this.props.step.anchorPrefix} />);
      }
    }

    return (
      <table>
        <tbody>
          <tr>
            {rows}
          </tr>
        </tbody>
      </table>
    );

  }
}

function mapStateToProps(state){
  return { step: state.step };
}

export default connect(mapStateToProps, actions)(NavMenu);



