import React from 'react';

let stFont = {fontSize: '60px'};
let stMarg = {marginBottom: '0px'};
let stTd = {padding: '5px 10px 5px 10px'};
let stFont30 = {fontSize: '30px'};
let stAlignTop = {verticalAlign: 'top'};

const StepFive = (props) => (
  <table>
    <tbody>
      <tr>
        <td style={stAlignTop}>
        <div className="text-center alert alert-success" style={stMarg}>React</div>
        <div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div>
        <div className="text-center alert alert-info" role="alert" style={stMarg}>Action</div>
        <div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div>
        </td>
        <td style={stTd}><table>
          <tbody>
            <tr><td>Example Action</td></tr>
            <tr><td>location: src/actions/steps.js</td></tr>
            <tr><td><br />In Redux there is Action and Action Creator.<br /><br />
            Action is the JavaScript object.<br />
            Action Creator is the wrapper function that returns the Action.<br /><br />
            This allows the Action Creator to be called from the Event fired on the React Component. An example of this is an OnClick event, where an user clicks a button and an action is dispatched.<br /><br />
            Dispatch is a method of the Store. (store.dispatch). However in the react-redux library the wiring of store.dispatch is handled for you in the connect function in the react component.<br /><br />
            This is why the Action Creator is passed into mapDispatchToProps. The Action Creator returns the Action which is dispatched to the Reducer.</td></tr>
            <tr><td><pre>{`export const CHANGE_STEP = 'CHANGE_STEP';

export function doChangeStep(val) {
    return {
        type: CHANGE_STEP,
        cargo: val
    };
}`}</pre></td></tr>
            <tr><td>In the above example, doChangeStep is the Action Creator and the returned object is the Action.
            <br /><br /><br />There can be many actions, 1 for each event on the React Component<br /></td></tr>
            <tr><td><pre>{`//ACTION
return {
    type: CHANGE_STEP,
    cargo: val
};`}</pre></td></tr>
            <tr><td><br />Note the construct of the Action.
            It is a plain JavaScript object consisting of 2 name value pairs.
            The first name is always type. This is defacto.
            The second name can change to whatever you like, though some Redux courses recomend payload.<br /><br/>
            The value of type is always a string.
            This can be within single quotes '' or as a constant (CHANGE_STEP).
            The convention of the string is that it is always in uppercase.</td></tr>
            </tbody>
          </table></td>
      </tr>
    </tbody>
  </table>
);

export default StepFive;



