import React from 'react';

let stFont = {fontSize: '60px'};
let stMarg = {marginBottom: '0px'};
let stTd = {padding: '5px 10px 5px 10px'};
let stFont30 = {fontSize: '30px'};
let stAlignTop = {verticalAlign: 'top'};
let stTopMarge = {verticalAlign: 'top',padding: '5px 10px 5px 10px'};

const StepSeven = (props) => (
  <table>
    <tbody>
      <tr>
        <td style={stAlignTop}>
          <div className="text-center alert alert-success" style={stMarg}>React</div>
          <div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div>
          <div className="text-center alert alert-info" role="alert" style={stMarg}>Action</div>
          <div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div>
          <div className="text-center alert alert-info" role="alert" style={stMarg}>Reducer</div>
          <div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div>
          <div className="text-center alert alert-info" role="alert" style={stMarg}>Store</div>
          <div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div>
          </td>
        <td style={stTopMarge}>
        <table>
        <tbody>
          <tr><td>Example Store</td></tr>
          <tr><td>location: src/index.js</td></tr>
          <tr><td>library: react, react-dom, react-redux, redux</td></tr>
          <tr><td><br />The Store is at the parent level where the ReactDOM element is assigned the the HTML.
          The Store is a property of the Provider from react-redux library.
          This automatically connects React and Redux. What you are not seeing is the Store, the state being connected to the app.
          What is also hidden is that the React component subscribes to the store to receive new updates.
          When an event is fired from the component and an action is dispatched to the reducer.
          The reducer then makes the change and updates the Store with the new state.
          The store then publishes the new states to all the subscribed components.
          So the short version is the Store acts as a publish and subscribe system.
          Notice in the above example that the reducer is a parameter of the createStore function.
          </td></tr>
          <tr><td><pre>{`import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { createStore } from 'redux';

import App from './App';
import reducer from './reducers';

let store = createStore(reducer);
//For debugging in chrome with redux tools.
//let store = createStore(reducer, window.devToolsExtension && window.devToolsExtension());

ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>,
  document.getElementById('container')
);`}</pre></td></tr>
            
          </tbody>
        </table>
        </td>
      </tr>
    </tbody>
  </table>
);

export default StepSeven;



