import React from 'react';

let stFont = {fontSize: '60px'};
let stMarg = {marginBottom: '0px'};
let stTd = {padding: '5px 10px 5px 10px'};
let stFont30 = {fontSize: '30px'};
let stAlignTop = {verticalAlign: 'top'};
let stTopMarge = {verticalAlign: 'top',padding: '5px 10px 5px 10px'};

const StepEight = (props) => (
  <table>
    <tbody>
      <tr>
      <td style={stAlignTop}>
        <div className="text-center alert alert-success" style={stMarg}>React</div>
        <div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div>
        <div className="text-center alert alert-warning" style={stMarg}>Middleware</div>
        <div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div>
        <div className="text-center alert alert-info" role="alert" style={stMarg}>Action</div>
        <div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div>
        <div className="text-center alert alert-info" role="alert" style={stMarg}>Reducer</div>
        <div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div>
        <div className="text-center alert alert-info" role="alert" style={stMarg}>Store</div>
        <div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div>
        <div className="text-center alert alert-success" style={stMarg}>React</div>
        </td>
        <td style={stTopMarge}><table>
        <tbody>
          <tr><td>One more thing: Middleware</td></tr>
          <tr><td><br />Middleware is an advanced part of Redux not covered in these basic steps.<br />
          What Middleware is, it is the ability to intercept Actions before they reach the Reducer.<br />
          By interecpting the actions Middleware has the ability to stop mulitple clicks from being actioned,
          request information from the server via Asynchronous calls. Think of Middleware as the ability to act as a gateway, either ignoring an action altogether or perfrom some other action.
          A basic Middleware would be to fetch new data from the server. the action would fired, be stopped by the Middleware, and then once the reposnse is received sent on to the reducer.
          As Middleware is beyond this quick intro to Redux there are lots of other resources on it to research.<br /><br />
          If you are familiar with Express.js Middleware then you grasp how Redux Middleware works.
          </td></tr>
          </tbody>
        </table></td>
      </tr>
    </tbody>
  </table>
);

export default StepEight;



