import React from 'react';

let stFont = {fontSize: '60px'};
let stMarg = {marginBottom: '0px'};
let stTd = {padding: '5px 10px 5px 10px'};

const StepThree = (props) => (
  <table>
    <tbody>
      <tr>
        <td><div className="text-center alert alert-success" style={stMarg}>React</div></td>
        <td></td>
      </tr>
      <tr>
        <td><div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div></td>
        <td></td>
      </tr>
      <tr>
        <td><div className="text-center alert alert-info" role="alert" style={stMarg}>Action</div></td>
        <td style={stTd}>2nd Principle of Redux: State is read-only<br />
          Dispatch an action for change. Example: type:'INCREMENT', cargo: 1</td>
      </tr>
      <tr>
        <td><div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div></td>
        <td></td>
      </tr>
      <tr>
        <td><div className="text-center alert alert-info" role="alert" style={stMarg}>Reducer</div></td>
        <td style={stTd}> 3rd Principle of Redux: Changes are made with pure functions<br />
        State of the app is written as a pure function called the Reducer, which returns an object (data).</td>
      </tr>
      <tr>
        <td><div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div></td>
        <td></td>
      </tr>
      <tr>
        <td><div className="text-center alert alert-info" role="alert" style={stMarg}>Store</div></td>
        <td style={stTd}> 1st Principle of Redux: Single source of truth<br />
        Everything that changes in your application including data and UI state is contained in a single object</td></tr>
      <tr>
        <td><div className="text-center"><i style={stFont} className="fa fa-long-arrow-down"></i></div></td>
        <td></td>
      </tr>
      <tr>
        <td><div className="text-center alert alert-success" style={stMarg}>React</div></td>
        <td></td>
      </tr>
    </tbody>
  </table>
);

export default StepThree;



